@import '/src/base.scss';

.main {
    display: flex;
}

.left {
    width: 200px;

    @media (max-width: $md) {
        width: 0;
    }
}

.right {
    width: calc(100vw - 250px);
    height: fit-content;
    padding: 0 20px;
    @media (max-width: $md) {
        width: 100vw;
    }
}

